.form-search {
  display: flex;
}
.input {
  width: 300px;
  padding: 10px;
  margin: 10px;
  border: none;
  border-radius: 4px;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}
.btn {
  color: white;
  background-color: rgba(255, 72, 0, 0.74);
  width: 80px;
  height: 100%;
  padding: 10px;
  margin-top: 10px;
  border: none;
  border-radius: 8px;
}
